import { useState, useEffect } from 'react';
import { useAuthDispatch, fetchData, UploadData } from '../../../../context';
import * as Styled from '../../../../components';
import * as utils from '../../../../utils';

export const RequestService = (props) => {
  const dispatch = useAuthDispatch();
  const [services, setServices] = useState([]);
  const [request, setRequest] = useState([]);
  const obj = { itemId: '', quantity: 0, toggle: 0 };
  useEffect(() => {
    async function get() {
      let data = await fetchData(dispatch, utils.remoteuri.getServices);
      if (data) setServices(data.services);
    }
    get();
  }, [dispatch]);
  function getserviceNameWithId(index) {
    for (let service of services) {
      if (service._id === request[index].itemId) return service.item;
    }
    return '';
  }
  const displayDiv = async (index, resetDesc) => {
    if (resetDesc) {
      let success = await handleItemChange(index);
      if (!success) return false;
    }
    resetToggler();
    request[index].toggle = 1;
    setRequest([...request]);
  };
  const resetToggler = () => {
    for (let obj of request) obj.toggle = 0;
  };
  const handleItemChange = async (index, e) => {
    if (e) {
      request[index].itemId = e.target.selectedOptions[0].getAttribute('_id');
      setRequest([...request]);
    }
    return true;
  };
  const appendModels = () => {
    let len = request.length;
    if (len && !request[len - 1].itemId) return;
    request.push(obj);
    displayDiv(request.length - 1, false);
  };
  const popModel = async () => {
    if (request.length) {
      request.pop();
      if (request.length) {
        resetToggler();
        request[request.length - 1].toggle = 1;
        let success = await handleItemChange(request.length - 1);
        if (!success) return false;
      }
    }
    setRequest([...request]);
  };
  const submitRequest = async () => {
    if (!request.length) {
      utils.createContextErrors('Invalid or Empty Request', dispatch);
      return;
    }
    const res = makeSureNoNulls(request, dispatch);
    if (res === null) return;
    const obj = { refMember: {}, items: res, isItem: false };
    let results = await UploadData(dispatch, obj, utils.remoteuri.request);
    if (results) {
      alert(results.message);
      setRequest([]);
    }
  };
  return (
    <Styled.VetView>
      <Styled.PairStruct>
        {request.map((entry, i) => (
          <div key={i}>
            {!request[i].toggle ? (
              <Styled.Table onClick={(e) => displayDiv(i, true)}>
                <Styled.TableB>{getserviceNameWithId(i)}</Styled.TableB>
              </Styled.Table>
            ) : (
              <Styled.ViewTask>
                <Styled.RequestForm>
                  <Styled.Card>
                    <Styled.Select
                      name='service'
                      value={request[i].itemId}
                      onChange={(e) => handleItemChange(i, e)}
                    >
                      <option value=''>Select A Service</option>
                      {services.map((service) => (
                        <option
                          key={service.code}
                          value={service._id}
                          _id={service._id}
                        >
                          {service.item}
                        </option>
                      ))}
                    </Styled.Select>
                  </Styled.Card>
                </Styled.RequestForm>
              </Styled.ViewTask>
            )}
          </div>
        ))}
        <Styled.ActionBar>
          <Styled.AddBtn type='button' onClick={(e) => appendModels()}>
            +
          </Styled.AddBtn>
          <Styled.RemoveBtn type='button' onClick={(e) => popModel()}>
            -
          </Styled.RemoveBtn>
        </Styled.ActionBar>
        <Styled.SubmitBtnCont>
          <Styled.SubmitReview type='button' onClick={(e) => submitRequest()}>
            Request
          </Styled.SubmitReview>
        </Styled.SubmitBtnCont>
        <utils.ViewErrorMessage />
      </Styled.PairStruct>
    </Styled.VetView>
  );
};

const makeSureNoNulls = (entries, dispatch) => {
  const newArr = [];
  for (let entry of entries) {
    if (!entry.itemId) {
      utils.createContextErrors('Error Attempting to submit null ', dispatch);
      return null;
    }
    let obj = { itemId: entry.itemId, quantity: entry.quantity };
    newArr.push(obj);
  }
  utils.clearContextErrors(dispatch);
  return newArr;
};
