import React, { useState, useEffect } from 'react';
import { Slider } from '../../../utils';
import { paths, remoteuri } from '../../../utils';
import { useAuthDispatch, fetchData } from '../../../context';
import * as Icons from '../../../images/svg/svg';
import * as Styled from '../../../components';

const MainView = (props) => {
  const [requests, setRequests] = useState(0);
  const dispatch = useAuthDispatch();
  const handleNav = (timeline) => {
    dispatch({ type: 'NAVIGATE', view: timeline });
    props.nav.history.push(paths.clientTimeline);
  };
  useEffect(() => {
    const doGet = async () => {
      let query = remoteuri.timeline + `?self=${true}&today=${true}`;
      let data = await fetchData(dispatch, query);
      if (data) setRequests(data.issued.length);
    };
    doGet();
  }, [dispatch]);

  return (
    <div className='card-view'>
      <Styled.CvTop>
        <Styled.CvTop1>
          <Styled.RequestTimeline onClick={(e) => handleNav('timeline')}>
            <span>
              <Icons.VhListUl size={40} />
            </span>
            <Styled.RequestTimelineInner>
              <strong>Request Timeline</strong>
              <div>
                <p style={{ lineHeight: '22px' }}>
                  <Styled.RequestTimelineChecked>
                    &#10004;
                  </Styled.RequestTimelineChecked>
                  <span>
                    You have <b>{requests}</b> initiated Request in the Meantime
                  </span>
                </p>
              </div>
            </Styled.RequestTimelineInner>
          </Styled.RequestTimeline>
          <Styled.RequestHistory onClick={(e) => handleNav('history')}>
            <span>
              <Icons.VhGift size={40} />
            </span>
            <Styled.RequestHistoryInner>
              <strong>History Review</strong>
              <div>
                <p>View your Request History.Give us your Review</p>
              </div>
            </Styled.RequestHistoryInner>
          </Styled.RequestHistory>
        </Styled.CvTop1>
        <Styled.CvTop2>
          <Styled.NewsSectionFlex>
            <Styled.NewsSectionHeader>
              <strong>Products Summary</strong>
            </Styled.NewsSectionHeader>
            <Slider component={NewsSectionSlideview} num={3} />
          </Styled.NewsSectionFlex>
          <Styled.NewsSectioncontent>
            <Styled.RssView>
              <Styled.Rsscontent>
                Consequat adipisicing irure cillum dolore velit amet mollit
                nisi.Nulla ex amet proident amet.
              </Styled.Rsscontent>
              <Styled.Rsscontent>
                Aliquip nulla aliqua ut labore cillum sint eiusmod ad mollit
                enim consectetur cillum. Officia occaecat pariatur..
              </Styled.Rsscontent>
            </Styled.RssView>
          </Styled.NewsSectioncontent>
        </Styled.CvTop2>
      </Styled.CvTop>
      <Styled.CCont>
        <Styled.AnimCard
          onClick={(e) => props.nav.history.push(paths.clientService)}
        >
          <Styled.H1>Post A Request</Styled.H1>
          <strong>Service</strong>
        </Styled.AnimCard>
        <Styled.AnimCard>
          <Styled.H1>view Items In Stock</Styled.H1>
          <strong>Stock</strong>
        </Styled.AnimCard>
        <Styled.AnimCard>
          <Styled.H1>View Personal Details</Styled.H1>
          <strong>Profile</strong>
        </Styled.AnimCard>
      </Styled.CCont>
    </div>
  );
};

const NewsSectionSlideview = (props) => {
  return (
    <Styled.NewsSectionImgs index={props.index}>
      <div
        style={{
          position: 'relative',
          top: '70%',
          left: '25%',
          width: '40%',
          height: '30%',
          zIndex: -10,
        }}
      >
        <Icons.VhImageCurveBottom />
      </div>
    </Styled.NewsSectionImgs>
  );
};

export default MainView;
