import React, { useState, useEffect } from 'react';
import { useAuthDispatch, fetchData, UploadData } from '../../../../context';
import * as Styled from '../../../../components';
import * as utils from '../../../../utils';
export const RequestItems = (props) => {
  const dispatch = useAuthDispatch();
  const [request, setRequest] = useState([]);
  const [items, setItems] = useState([]);
  const [desc, setDesc] = useState([]);

  const selectModel = [
    { value: 1, text: 'Piece' },
    { value: 2, text: 'Pair' },
    { value: 5, text: 'Group of 5' },
    { value: 10, text: 'Group of 10' },
    { value: 12, text: 'Dozen' },
    { value: 25, text: 'Crate' },
  ];
  function getVal(i) {
    if (!request[i].quantifier) return;
    for (let obj of selectModel) {
      if (obj.value === Number(request[i].quantifier))
        return 'of ' + obj.text + 's';
    }
  }
  function getitemNameWithId(index) {
    for (let item of items) {
      if (item._id === request[index].itemId) return item.item;
    }
    return '';
  }
  useEffect(() => {
    async function get() {
      let data = await fetchData(dispatch, utils.remoteuri.getItems);
      if (data) setItems(data.items);
    }
    get();
  }, [dispatch]);
  const obj = {
    itemId: '',
    description: '',
    quantity: 0,
    dsQuantity: 0,
    quantifier: 1,
    toggle: 0,
  };
  const resetToggler = () => {
    for (let obj of request) obj.toggle = 0;
  };
  const displayDiv = async (index, resetDesc) => {
    if (resetDesc) {
      let success = await handleItemChange(index);
      if (!success) return false;
    }
    resetToggler();
    request[index].toggle = 1;
    setRequest([...request]);
  };
  const appendModels = () => {
    let len = request.length;
    if (len && !request[len - 1].itemId) return;
    request.push(obj);
    displayDiv(request.length - 1, false);
  };
  const handleItemChange = async (index, e) => {
    if (e) {
      request[index].itemId = e.target.selectedOptions[0].getAttribute('_id');
      setRequest([...request]);
    }
    if (request[index].itemId) {
      let descriptives = await fetchData(
        dispatch,
        utils.remoteuri.getDescriptives,
        request[index].itemId
      );
      if (!descriptives) return false;
      setDesc(descriptives.descriptives);
    }
    return true;
  };
  const handleDsQuantityChange = (index, e) => {
    if (e) request[index].dsQuantity = e.target.value;
    if (request[index].dsQuantity < 0) return;
    const total = request[index].dsQuantity * request[index].quantifier;
    request[index].quantity = total;
    setRequest([...request]);
  };
  const handleQuantifierChange = (index, e) => {
    request[index].quantifier = e.target.value;
    setRequest([...request]);
    handleDsQuantityChange(index);
  };
  const handleDescChange = (index, e) => {
    request[index].description =
      e.target.selectedOptions[0].getAttribute('desc');
    setRequest([...request]);
  };
  const popModel = async () => {
    if (request.length) {
      request.pop();
      if (request.length) {
        resetToggler();
        request[request.length - 1].toggle = 1;
        let success = await handleItemChange(request.length - 1);
        if (!success) return false;
      }
    }
    setRequest([...request]);
  };

  const submitRequest = async () => {
    if (!request.length) {
      utils.createContextErrors('Invalid or Empty Request', dispatch);
      return;
    }
    const res = makeSureNoNulls(request, dispatch);
    if (res === null) return;
    const obj = { refMember: {}, items: res, isItem: true };
    let results = await UploadData(dispatch, obj, utils.remoteuri.request);
    if (results) {
      if (!results.data.length) setRequest([]);
      else {
        setRequest(manipulateArr(results.data, dispatch));
      }
      alert(results.message);
    }
  };
  return (
    <Styled.VetView>
      <Styled.PairStruct>
        {request.map((entry, i) => (
          <div key={i}>
            {!request[i].toggle ? (
              <Styled.Table onClick={(e) => displayDiv(i, true)}>
                <Styled.TableB>
                  {request[i].description +
                    '  ' +
                    getitemNameWithId(i) +
                    '\t:\t' +
                    request[i].quantity}
                </Styled.TableB>
              </Styled.Table>
            ) : (
              <Styled.ViewTask>
                <Styled.RequestedView>
                  <Styled.Card>
                    <Styled.Select
                      data-testid='item'
                      name='item'
                      value={request[i].itemId}
                      onChange={(e) => handleItemChange(i, e)}
                    >
                      <option value=''>Select Item</option>
                      {items.map((item) => (
                        <option key={item._id} value={item._id} _id={item._id}>
                          {item.item}
                        </option>
                      ))}
                    </Styled.Select>
                  </Styled.Card>
                  <Styled.Card>
                    <strong style={{ color: 'green' }}>
                      Number {getVal(i)}
                    </strong>
                  </Styled.Card>
                  <Styled.Card>
                    <Styled.Input
                      type='number'
                      name='amount'
                      value={request[i].dsQuantity}
                      onChange={(e) => handleDsQuantityChange(i, e)}
                    />
                  </Styled.Card>
                </Styled.RequestedView>
                <Styled.RequestForm>
                  <Styled.Card>
                    {request[i].toggle ? (
                      <Styled.Select
                        data-testid='desc'
                        name='desc'
                        value={request[i].description}
                        onChange={(e) => handleDescChange(i, e)}
                      >
                        <option value=''>Description</option>
                        {desc.map((descriptive) => (
                          <option
                            key={descriptive._id}
                            value={descriptive.description}
                            desc={descriptive.description}
                          >
                            {descriptive.description}
                          </option>
                        ))}
                      </Styled.Select>
                    ) : (
                      <Styled.Card>
                        <strong style={{ color: 'green' }}>
                          {request[i].description}
                        </strong>
                      </Styled.Card>
                    )}
                  </Styled.Card>
                  <Styled.Card>
                    <strong style={{ color: 'green' }}>
                      Total Items:{request[i].quantity}
                    </strong>
                  </Styled.Card>
                </Styled.RequestForm>
                <Styled.RequestForm>
                  <Styled.Card>
                    <Styled.Select
                      name='Quantifier'
                      value={request[i].quantifier}
                      onChange={(e) => handleQuantifierChange(i, e)}
                    >
                      <option value={1}>Please Select Quantifier</option>
                      {selectModel.map((opt) => (
                        <option key={opt.value} value={opt.value}>
                          {opt.text}
                        </option>
                      ))}
                    </Styled.Select>
                  </Styled.Card>
                </Styled.RequestForm>
              </Styled.ViewTask>
            )}
          </div>
        ))}
        <Styled.ActionBar>
          <Styled.AddBtn type='button' onClick={(e) => appendModels()}>
            +
          </Styled.AddBtn>
          <Styled.RemoveBtn type='button' onClick={(e) => popModel()}>
            -
          </Styled.RemoveBtn>
        </Styled.ActionBar>
        <Styled.SubmitBtnCont>
          <Styled.SubmitReview type='button' onClick={(e) => submitRequest()}>
            Request
          </Styled.SubmitReview>
        </Styled.SubmitBtnCont>
        <utils.ViewErrorMessage />
      </Styled.PairStruct>
    </Styled.VetView>
  );
};

const makeSureNoNulls = (entries, dispatch) => {
  const newArr = [];
  for (let entry of entries) {
    if (!entry.itemId) {
      utils.createContextErrors('Error Attempting to submit null ', dispatch);
      return null;
    }
    let obj = {
      itemId: entry.itemId,
      quantity: entry.quantity,
      description: entry.description,
    };
    newArr.push(obj);
  }
  utils.clearContextErrors(dispatch);
  return newArr;
};

const manipulateArr = (entries, dispatch) => {
  for (let entry of entries) {
    entry['toggle'] = 0;
    entry['quantity'] = 0;
    entry['dsQuantity'] = 0;
    entry['quantifier'] = 0;
  }
  utils.createContextErrors('Above items could not be processed', dispatch);
  return entries;
};
