import React, { useState, useEffect } from 'react';
import { useAuthDispatch, fetchData, useAuthState } from '../../../../context';
import * as Styled from '../../../../components';
import * as utils from '../../../../utils';

export const ViewTimeline = (props) => {
  const dispatch = useAuthDispatch();
  const { view } = useAuthState();
  const [timeline, setTimeline] = useState([]);
  function getItem(i) {
    if (timeline[i].assetId)
      return `${timeline[i].assetId.description} ${timeline[i].itemId.item}`;
    return timeline[i].itemId.item;
  }
  useEffect(() => {
    const doGet = async () => {
      let query = utils.remoteuri.timeline + `?self=${true}`;
      if (view === 'timeline') query = query + `&today=${true}`;
      else query = query + `&today=${false}`;
      let data = await fetchData(dispatch, query);
      if (data) setTimeline(data.issued);
    };
    doGet();
  }, [dispatch, view]);
  return (
    <Styled.VetView>
      <Styled.PairStruct>
        {timeline.map((request, i) => (
          <div key={i}>
            <Styled.Table key={i}>
              <Styled.TableB>
                {' '}
                {`${i}\t${new Date(
                  request.issuedAt
                ).toLocaleString()}\t${getItem(i)}\t${request.quantity}\t ${
                  request.approved ? 'Approved' : 'Pending'
                }`}
              </Styled.TableB>
            </Styled.Table>
          </div>
        ))}
      </Styled.PairStruct>
    </Styled.VetView>
  );
};
