import { Login, PageNotFound } from '../views/public';
import * as Private from '../views/private';
import { paths } from '../utils';
import { routePass } from '../context';

export const routes = [
  {
    path: paths.rootLogin,
    component: Login,
    isPrivate: false,
    pass: '',
  },
  {
    path: paths.clientHome,
    component: Private.Client,
    isPrivate: true,
    pass: routePass.client,
  },
  {
    path: paths.clientReq,
    component: Private.Request,
    isPrivate: true,
    pass: routePass.client,
  },
  {
    path: paths.clientService,
    component: Private.Service,
    isPrivate: true,
    pass: routePass.client,
  },
  {
    path: paths.clientTimeline,
    component: Private.Timeline,
    isPrivate: true,
    pass: routePass.client,
  },
  {
    path: paths.clientReset,
    component: Private.Reset,
    isPrivate: true,
    pass: routePass.client,
  },
  {
    path: paths.notFound,
    component: PageNotFound,
    isPrivate: true,
    pass: '',
  },
];
